html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: $principal_font;
  font-size: 1.6rem;
}
p {
  color: $black;
  font-size: 2rem;
}
.container {
  width: 90%;
  max-width: 1920px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
  font-weight: 900;
  color: $blackLight;
  text-align: center;
}

